import React, {Component} from 'react';
import 'materialize-css/dist/css/materialize.min.css';
import { Link } from 'react-router-dom';
import TiExport from 'react-icons/lib/ti/export';

class Footer extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <footer className="page-footer" style={{marginLeft: this.props.isDrawerOpen ? '256px' : '0px'}}>
        <div className="container">
          <div className="row">
            <div className="col l6 s12">
              <Link className="textDecorationNone" to="/" replace><img style={{width: '68px'}} src='https://image.ibb.co/fjd77x/logo_updated.jpg' alt="Conestoga's Web Enthusiasts" /></Link>
              <p className="grey-text text-lighten-4">Conestoga's Web Enthusiasts (CWE) is a Usability tutorial website that provides tutorials of usability and usability testing.</p>
            </div>
            <div className="col l4 offset-l2 s12">
              <h5 className="white-text">Popular Topics</h5>
              <ul style={{marginLeft: '25px'}}>
                <li><Link style={{color:'white'}} className="textDecorationNone" to="/usability" replace>Web Usability <TiExport className="exportLink"/></Link></li>
                <li><Link style={{color:'white'}} className="textDecorationNone" to="/usabilityTesting" replace>Web Usability Testing<TiExport className="exportLink"/></Link></li>
                {/*<li><a className="grey-text text-lighten-3" href="#!">Link 3</a></li>*/}
                {/*<li><a className="grey-text text-lighten-3" href="#!">Link 4</a></li>*/}
              </ul>
            </div>
          </div>
        </div>
        <div className="footer-copyright">
          <div className="container">
            {/* Copyright 2018 CWE*/}
            {/*<a className="grey-text text-lighten-4 right" href="#!">More Links</a>*/}
          </div>
        </div>
      </footer>
    );
  }
}

export default Footer;



