import React, {Component} from 'react';
import AppBar from 'material-ui/AppBar';
import {Router, Route, Switch} from 'react-router-dom';
import Drawer from 'material-ui/Drawer';
import createHashHistory from 'history/createHashHistory';

import {Link} from 'react-router-dom';
import './App.css';
import './custom.css';
import MenuContainer from './components/MenuContainer';
import MainContent from './components/MainContent';
import Footer from './components/Footer';
import Usability from './components/Usability';
import UsabilityTesting from './components/UsabilityTesting';


const history = createHashHistory();
let prevLocation = {};
history.listen(location => {
  setTimeout(() => {
    const pathChanged = prevLocation.pathname !== location.pathname;
    const hashChanged = prevLocation.hash !== location.hash;
    if (pathChanged || hashChanged) window.scrollTo(0, 0);
    prevLocation = location;
  });
});

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isDrawerOpen: false
    }
  }


  update = () => {
    console.log('componentDidUpdate()');
  };

  toggleSideBar = () => {
    this.setState({
      isDrawerOpen: !this.state.isDrawerOpen
    });
  };

  render() {
    return (
      <Router history={history} onUpdate={() => this.update}>
        <div>
          <AppBar title={<div><a className="skipMain" href="/usability/">Skip to main content</a><Link className="textDecorationNone" to="/" replace><img style={{width: '68px'}}
                                                                                  src='https://image.ibb.co/fjd77x/logo_updated.jpg' alt="Conestoga's Web Enthusiasts"/></Link></div>}
                  style={{position: 'fixed', top: 0, color: 'white'}}
                  iconElementRight={<input id="searchInput" placeholder='&#xf002; Search'
                                           style={{
                                             height: '33px',
                                             border: '1.25px solid white',
                                             background: 'transparent',
                                             color: 'white', width: '75%'
                                           }}/>
                  } onLeftIconButtonTouchTap={this.toggleSideBar}
          />
          <Drawer open={this.state.isDrawerOpen} containerStyle={{marginTop: '65px'}}>
            <MenuContainer toggleSideBar={this.toggleSideBar}/>
          </Drawer>
          <Switch>
            <Route exact path="/" component={() => <MainContent isDrawerOpen={this.state.isDrawerOpen}/>}/>
            <Route path="/usability" component={() => <Usability isDrawerOpen={this.state.isDrawerOpen}/>}/>
            <Route path="/usabilityTesting"
                   component={() => <UsabilityTesting isDrawerOpen={this.state.isDrawerOpen}/>}/>
          </Switch>
          <Footer isDrawerOpen={this.state.isDrawerOpen}/>
        </div>
      </Router>
    );
  }
}

export default App;



