import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import TiChevronRight from 'react-icons/lib/ti/chevron-right';

// import YouTube from 'react-youtube';

class UsabilityTesting extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div style={{
        marginLeft: this.props.isDrawerOpen ? '300px' : '50px', marginTop: '66px',
        maxWidth: '90%', padding: '1% 3% 3% 2%'
      }}>
        <div>
          <ul className="breadcrumb" style={{padding: '15px 21px'}}>
            <Link className="textDecorationNone" to="/" replace><li>Home</li></Link>
            <TiChevronRight style={{color: '#a5abb1'}}/>
            <li>Web Usability Testing</li>
          </ul>
        </div>
        <h3 id="main"><b>Introduction To Usability Testing:</b></h3>
        <p>Usability Testing does not mean monitoring your application with orthodox, quantitative analytics. Usability
          Testing is now more of qualitative than quantitative. Let's learn how to perform web usability testing on you
          web apps. If you are not familiar with the term Web Usability, then you should checkout our <Link
            className="textDecorationNone" to="/usability" replace>Web Usability</Link> Tutorials.</p>
        <p>Quality Testing is done by focusing on insights. You should focus on serious problems as they are easy to
          find. Analytical tools indicates user's behaviour, but they do not tell why they are behaving like that.</p>
        <p>Pre-scheduled tests are good for the quality of the app. Initial releases are not presentable but it will be
          beneficial if we do so.</p>
        <p>It is better to learn from your own mistakes. Also, learning from your competitors' mistakes will keep you
          one step ahead from them.
        </p>
        <h4><b>Finding potential users for Testing:</b></h4>
        <p>Usability Testing focuses on what users think about the app, but not on their feedback or opinion.
          Users with domain knowledge must be preferred.
          Screening of the invited users for testing workshop to find potential users, can give you valuable insights of
          your app.
          Also, avoid same users for more than one usability testing workshop.
          Testing sessions are a joint effort from users, developers and designers with a common goal to find valuable
          insights of the application.
        </p>
        <p>According to Andy Zaidman, his son is the best tester in the world. His son throws away stuff in various
          places and observe what happens. The behaviour of Andy's son can be compared with the playful attitude of a
          user.
          Take a look at his <b>Tedx</b> speech.
        </p>
        <iframe style={{width: '65%', height: '315px'}} src="https://www.youtube.com/embed/IfXVEz_mMHI"
                allowFullScreen></iframe>
        <div style={{width: '65%'}}><a href="https://www.youtube.com/watch?v=IfXVEz_mMHI" target="__blank" alt="TEDxTalks. Making Testing Fun | Andy Zaidman | TEDxDelft.">Source:</a>TEDxTalks. Making Testing Fun | Andy Zaidman | TEDxDelft. YouTube,
          YouTube, 26 Mar. 2015, www.youtube.com/watch?v=IfXVEz_mMHI. Web. March 14, 2018
        </div>
        <h4><b>Role of Stakeholders:</b></h4>
        <p>Stakeholders like sponsors must attend testing workshops instead of watching recorded sessions. Inputs from
          stakeholders are important for the usability testing.
          Observers play an important role in such testing sessions as their opinions and suggestions are the key to
          success for usability test sessions.
        </p>
        <p>
          An important aspect of testing session is to find as many problems as possible instead of thinking about how
          to solve them.
          Once the problems are listed, then you should focus on prioritizing the usability problems.
          Number of problems are most likely more than the resources required to solve them. All usability problems can
          not be solved in the initial release.
        </p>
        <h5><b>Things to remember about usability testing:</b></h5>
        <div style={{marginLeft: '45px'}}>
          <ul style={{fontSize: 'medium'}}>
            <li>Always try to implement simple and small solutions before going for redesigning the whole prototype.
            </li>
            <li>Visual differentiation is important as significant things must be accessed easily.</li>
            <li>Remote testing is a cheap but less efficient.</li>
          </ul>
        </div>
      </div>
    );
  }
}

export default UsabilityTesting;



