import React, {Component} from 'react';

import RaisedButton from 'material-ui/RaisedButton';
import {Flex, Box} from 'grid-styled';
import { Link } from 'react-router-dom';

import Typist from 'react-typist';
import particleOption from "../particles";

import 'particles.js';
import TiExport from 'react-icons/lib/ti/export';

// import Logo from './images/3_updated_new.jpg';
class MainContent extends Component {

  constructor(props) {
    super(props);
  }

  componentDidMount() {
    window.particlesJS('particles-js', particleOption, () => {
    });
  }

  render() {
    console.log('this.props', this.props);
    return (
      <div style={{marginLeft: this.props.isDrawerOpen ? '256px' : '0px'}}>
        <div id="particles-js">
          <Typist startDelay={100} stdTypingDelay={70} blink={true} avgTypingDelay={200}
                  cursor={{
                    show: true,
                    blink: true
                  }}>
            <br/>
            <Typist.Delay ms={500}/>
            <br/>
            <span>Conestoga's Web Enthusiasts...</span>
          </Typist>
        </div>
        <div style={{ padding: '50px', textAlign: 'center'}}>
          <h3>Web Usability</h3>
          <Link className="textDecorationNone" to="/usability" replace><RaisedButton children={<span style={{ padding: '10px', color: 'white'}} key={1}>Get Started <TiExport className="exportLink"/></span>} primary={true} buttonStyle={{backgroundColor : "#666666"}}/></Link>
        </div>
        <Flex style={{margin: '0 40px 35px 60px',padding: '20px'}}>
          <Box width={1 / 2} p={10}>
            <h4>Usability</h4>
            <p>Easy to use web designs with interactive user experience is what we call <i>Usability</i>. Usability decides whether user will stick to your website or go for some better options.</p>
            <p><Link className="textDecorationNone" to="/usability" replace>This<TiExport className="exportLink"/></Link> tutorial will help you to gain insight of Web Usability and helps you to build some awesome designs.</p>
            <p>You can also checkout out <Link className="textDecorationNone" to="/usabilityTesting" replace>Web Usability Testing<TiExport className="exportLink"/></Link> tutorial.</p>
          </Box>
          <Box width={1 / 2} p={10}>
            <h4>Usability Testing</h4>
            <p>Web/App Development and Testing go hand in hand. Usability Testing does not mean monitoring your application with orthodox, quantitative analytics. Usability Testing is now more of qualitative than quantitative.</p>
            <p>Let's learn how to perform web usability testing on you web apps.</p>
            <p>If you are not familiar with the term Web Usability, then you should checkout our <Link className="textDecorationNone" to="/usability" replace>Web Usability<TiExport className="exportLink"/></Link> Tutorials.</p>
          </Box>
        </Flex>
      </div>
    );
  }
}

export default MainContent;


