import React from 'react';
import MenuItem from 'material-ui/MenuItem';
import {Link} from 'react-router-dom';

class MenuContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      backgroundColor: 0
    }
  }

  componentDidMount() {
    console.log(window.location.hash, this.state.backgroundColor);
    this.changeBackGround(0);
  }

  changeBackGround = (num) => {
    console.log(num);
    if (num) {
      this.setState({
        backgroundColor: num
      });
    }
    // } else {
    //
    //   if (window.location.hash.toLowerCase().includes('usabilityTesting')) {
    //     this.setState({
    //       backgroundColor: 2
    //     });
    //   } else if (window.location.hash.toLowerCase().includes('usability')) {
    //     this.setState({
    //       backgroundColor: 1
    //     });
    //   } else {
    //     this.setState({
    //       backgroundColor: 0
    //     });
    //   }
    // }
  };

  render() {
    return (
      <div>
        <Link className="textDecorationNone" to="/usability" replace><MenuItem>Web
          Usability</MenuItem></Link>
        <Link className="textDecorationNone" to="/usabilityTesting" replace><MenuItem>Web
          Usability
          Testing</MenuItem></Link>
      </div>
    )
  }
}

export default MenuContainer;



